import React from 'react';
import recipes from "./food.json";
import { Convert } from "./RecipeParser";
import { useTheme } from '@mui/material/styles';

import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import Alert from '@mui/material/Alert';
import AlertTitle from '@mui/material/AlertTitle';

import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos';

const NoMatches = () => {
    <Box>
        <Alert severity="warning">
            <AlertTitle>Warning</AlertTitle>
            This is a warning alert  <strong>check it out!</strong>
        </Alert>
    </Box>
}

const parseRecipes = Convert.toRecipe(JSON.stringify(recipes));


const RecipesPage = (query) => {

    let recipesCount = 0;
    let searchString = query.query.toString();
    const theme = useTheme();

    return (
        <>
            <br />
            <Container>
                <Row xs={1} md={2} lg={3} xl={4}>
                    {parseRecipes.filter((r) => {
                        if (searchString === '') {
                            recipesCount++
                            return r
                        } else if (r.title.toLowerCase().includes(searchString.toLowerCase())) {
                            recipesCount++
                            return r
                        } else if (r.ingredients.toString().toLowerCase().includes(searchString.toLowerCase())) {
                            recipesCount++
                            return r
                        } else if (r.instructions.toString().toLowerCase().includes(searchString.toLowerCase())) {
                            recipesCount++
                            return r
                        }
                    }).map((r) =>
                        <Col>
                            <br />
                            <Card sx={{ display: 'flex' }}>
                                {/* <CardMedia
                                    component="img"
                                    sx={{ width: 151 }}
                                    image="./static/r.jpg"
                                    alt="Live from space album cover"
                                /> */}
                                <Box sx={{ display: 'flex', flexDirection: 'column', width: "95%" }}>
                                    <CardContent sx={{ flex: '1 0 auto' }}>
                                        <Typography component="div" variant="h7">
                                            {r.title}
                                        </Typography>
                                        <Typography variant="subtitle1" color="text.secondary" component="h2">
                                            Likes {r.likes}
                                        </Typography>
                                        <Typography variant="subtitle1" color="text.secondary" component="h2">
                                            Dislikes {r.dislikes}
                                        </Typography>
                                    </CardContent>
                                </Box>
                                <Box sx={{ margin: 'auto' }}>
                                    <IconButton sx={{color: "#0d6efd"}} aria-label="goto">
                                        <ArrowForwardIosIcon sx={{ height: 38, width: 38 }} />
                                    </IconButton>
                                </Box>
                            </Card>
                        </Col>
                    )}
                </Row>
            </Container>
        </>
    )
};

export default RecipesPage;
